/****************ARROW*****************/
.arrow {
  text-align: center;
  position: relative;
  padding-top: 20px;
  margin-bottom: 40px;
}

.arrow::after {
  content: "";
  position: absolute;
  background-color: #f5f5f5;
  top: 0;
  left: 0;
  width: 100%;
  height: 200px;
  z-index: -1;
}

.arrow .arrow-chairs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.arrow .arrow-chairs .arrow-chair {
  text-align: center;
  width: 20%;
}

.arrow .arrow-chairs .arrow-chair a img {
  display: block;
  width: 100%;
  transition: transform 0.5s ease;
}

.arrow .arrow-chairs .arrow-chair a img:hover {
  transform: scale(1.2);
}

.arrow .arrow-chairs .arrow-chair a h4 {
  color: #000000;
  margin-top: 30px;
}

.arrow .arrow-chairs .arrow-chair a span {
  color: #969696;
  display: block;
  margin: 10px 0;
}

.arrow .arrow-chairs .arrow-chair a .arrow-stars {
  margin-bottom: 20px;
}

.arrow .arrow-chairs .arrow-chair a .arrow-stars i {
  margin-right: 3px;
  color: #969696;
}

.arrow .arrow-chairs .arrow-chair a .arrow-stars i:hover {
  color: #00ffff;
}

.arrow .left-right-arrows {
  cursor: pointer;
}

.arrow .left-right-arrows .arrow-left {
  top: 35%;
  left: 15%;
  transform: translate(-15%, -35%);
  position: absolute;
  padding: 7px 14px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  border-radius: 2px;
}

.arrow .left-right-arrows .arrow-right {
  top: 35%;
  right: 15%;
  transform: translate(-15%, -35%);
  position: absolute;
  padding: 7px 14px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  border-radius: 2px;
}

/***************SHOP-NOW****************/
.shop-now {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.shop-now .shop-now__category {
  position: relative;
  width: 33.33%;
}

.shop-now .shop-now__category::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
}

.shop-now .shop-now__category img {
  width: 100%;
  height: auto;
}

.shop-now .shop-now__category .shop-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #ffffff;
  z-index: 1;
}

.shop-now .shop-now__category .shop-content h3 {
  margin-bottom: 40px;
}

.shop-now .shop-now__category .shop-content a {
  display: inline-block;
  color: #ffffff;
  border: 2px solid #ffffff;
  padding: 10px 15px;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  min-width: 120px;
}

.shop-now .shop-now__category .shop-content a:hover {
  background-color: #ffffff;
  color: #969696;
}

/************PRODUCT-FEATURES**********/
.product-features {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.product-features .product-features__item .product-features__item-holder {
  display: flex;
  flex-direction: row;
  flex: 1 1 25em;
}

.product-features .product-features__item .product-features__item-holder .product-features__item__image {
  overflow: hidden;
}

.product-features .product-features__item .product-features__item-holder .product-features__item__image img {
  max-width: 170px;
  margin: 15px;
  transition: transform 0.5s ease;
}

.product-features .product-features__item .product-features__item-holder .product-features__item__image img:hover {
  transform: scale(1.1);
}

.product-features .product-features__item .product-features__item-holder .product-features__item__text .item__text__holder {
  margin: 15px 0;
}

.product-features .product-features__item .product-features__item-holder .product-features__item__text .item__text__holder p {
  color: #969696;
  margin-top: 10px;
}

.product-features .product-features__item .product-features__item-holder .product-features__item__text .item__text__holder p:first-child {
  font-weight: bold;
  font-size: 1.1rem;
  color: #000000;
}

/**************HOVER-STARS*************/
.product-stars {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 65px;
}

.product-stars .star {
  margin-right: 3px;
  cursor: pointer;
}

.product-stars .star svg {
  width: 12px;
  fill: #dddddd;
}

.product-stars .star:hover svg {
  fill: #dddddd;
}

.product-stars .star:hover svg,
.product-stars .star:hover ~ .star svg {
  fill: #00ffff;
}

/**************LATEST PRODUCT************/
.latest-product__poster {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.latest-product__poster .poster-images {
  display: flex;
  flex-direction: row;
  overflow: hidden;
  max-width: 25%;
}

.latest-product__poster .poster-images img {
  width: 100%;
  transition: transform 0.5s ease;
}

.latest-product__poster .poster-images img:hover {
  transform: scale(1.15);
}

/***************ABOUT US***************/
.about-us__content .about-us__information {
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
  justify-content: center;
}

.about-us__content .about-us__information .we-are-delphinus {
  width: 60%;
  background: url(../../assets/images/background/background-1.jpg) no-repeat center center/cover;
  color: #ffffff;
}

.about-us__content .about-us__information .we-are-delphinus .we-are-delphinus__holder {
  padding: 45px 35px 25px 35px;
  position: relative;
}

.about-us__content .about-us__information .we-are-delphinus .we-are-delphinus__holder h3 {
  margin-bottom: 30px;
}

.about-us__content .about-us__information .we-are-delphinus .we-are-delphinus__holder .about-delphinus-description {
  margin-bottom: 30px;
}

.about-us__content .about-us__information .we-are-delphinus .we-are-delphinus__holder p {
  margin-bottom: .15rem;
}

.about-us__content .about-us__information .we-are-delphinus .we-are-delphinus__holder p:last-child {
  margin-top: 0;
}

.about-us__content .about-us__information .newsletter {
  width: 30%;
}

.about-us__content .about-us__information .newsletter .newsletter__holder {
  text-align: center;
  margin-left: 20px;
}

.about-us__content .about-us__information .newsletter .newsletter__holder .newsletter__content {
  border: 2px solid #dddddd;
  padding: 45px 35px 30px 35px;
}

.about-us__content .about-us__information .newsletter .newsletter__holder .newsletter__content h3 {
  margin-bottom: 30px;
}

.about-us__content .about-us__information .newsletter .newsletter__holder .newsletter__content form {
  position: relative;
  margin: 35px 0 57px 0;
}

.about-us__content .about-us__information .newsletter .newsletter__holder .newsletter__content form input {
  border: 2px solid #dddddd;
  padding: 10px 29px 10px 10px;
  text-indent: 5px;
  width: 90%;
}

.about-us__content .about-us__information .newsletter .newsletter__holder .newsletter__content button {
  position: absolute;
  top: 10px;
  right: 25px;
  background: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.about-us__content .about-us__poster {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.about-us__content .about-us__poster .about-us__poster-images {
  max-width: 30%;
  flex-grow: 1;
}

.about-us__content .about-us__poster .about-us__poster-images .poster-images__holder {
  margin-left: 20px;
  overflow: hidden;
}

.about-us__content .about-us__poster .about-us__poster-images .poster-images__holder img {
  width: 100%;
  transition: transform 0.5s ease;
  min-height: 350px;
  max-height: 350px;
  display: block;
}

.about-us__content .about-us__poster .about-us__poster-images .poster-images__holder img:hover {
  transform: scale(1.2);
}

.about-us__content .about-us__poster .about-us__poster-images .first-poster {
  margin-left: 0;
}

.about-us__brands {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 40px 0;
}

.about-us__brands .brands {
  opacity: 50%;
  margin-left: 20px;
}

.about-us__brands .brands:first-child {
  margin-left: 0;
}

.about-us__brands .brands img {
  transition: transform 0.5s ease;
}

.about-us__brands .brands img:hover {
  cursor: pointer;
  transform: scale(1.3);
}

/***************SHIPPING**************/
.free-shipping {
  background-color: #f5f5f5;
  text-align: center;
}

.free-shipping .shippings {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.free-shipping .shippings .shipping {
  width: 45%;
}

.free-shipping .shippings .shipping:first-child .shipping-holder {
  margin-left: 0;
}

.free-shipping .shippings .shipping .shipping-holder {
  margin: 60px 0;
  padding: 35px 40px;
  background-color: #ffffff;
  font-size: 2rem;
  transition: all 0.5s ease;
  margin-left: 30px;
}

.free-shipping .shippings .shipping .shipping-holder:hover {
  background-color: #969696;
}

.free-shipping .shippings .shipping .shipping-holder:hover a {
  color: #ffffff;
}

.free-shipping .shippings .shipping .shipping-holder a {
  color: #000000;
}

/*****************HEADER***************/
