}

.header-bottom .header-bottom__holder .search {
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

/****************MOBILE-HEADER***************/
#mobile-header {
  display: none;
  padding: 15px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: #ffffff;
}

#mobile-header .mobile-header__holder {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#mobile-header .mobile-header__holder .mobile-header__left-side ul,
#mobile-header .mobile-header__holder .mobile-header__right-side ul {
  display: flex;
  flex-direction: row;
}

#mobile-header .mobile-header__holder .mobile-header__left-side ul li,
#mobile-header .mobile-header__holder .mobile-header__right-side ul li {
  padding: 7px 10px;
  font-size: 1.3rem;
  margin: 0 5px;
}

#mobile-header .mobile-header__holder .mobile-header__left-side .icon-left-section .basket a {
  position: relative;
}

#mobile-header .mobile-header__holder .mobile-header__left-side .icon-left-section .basket a span {
  position: absolute;
  right: -10px;
  top: -10px;
  background: #ff0000;
  color: #ffffff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  font-size: 0.8rem;
  z-index: 1;
  line-height: 1.5rem;
  text-align: center;
}

#mobile-header .mobile-header__holder .mobile-header__logo {
  margin-top: 10px;
}

#mobile-header .mobile-header__holder .mobile-header__right-side .icon-right-section li:last-child {
  background: #f5f5f5;
  border-radius: 5px;
  margin-left: 10px;
}

/* Footer
* ===============
*
* Place all your footer declarations here */
/***************FOOTER***************/
.container-holder {
  padding: 50px 0;
}

.container-holder .footer-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.container-holder .footer-list .footer-list__content h3 {
  margin-bottom: 20px;
}

.container-holder .footer-list .footer-list__content ul {
  line-height: 1.7rem;
}

.contact-us {
  background-color: #f5f5f5;
  height: 120px;
  width: 100%;
}

.contact-us .contact-us__content {
  text-align: center;
}

.contact-us .contact-us__content .contact-us__app a {
  padding: 30px;
  color: #969696;
}

.contact-us .contact-us__content .contact-us__app a i {
  margin: 40px 0 20px 0;
  max-width: 7px;
}

.contact-us .contact-us__content .contact-us__app a i:hover {
  color: #000000;
}

.contact-us .contact-us__content span {
  color: #969696;
}

/**************SECTION-TITLE*************/
.section-title {
  position: relative;
  padding: 100px 0 60px 0;
  text-align: center;
}

.section-title h2 {
  padding: 5px 0 15px 0;
}

.section-title::after {
  content: "";
  background: url(../../assets/images/svg/broken-line.svg);
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  top: 15px;
  height: 30px;
  background-repeat: no-repeat;
  background-size: 50px;
  background-position: center;
  margin-top: 40px;
}

